	//import News from '../components/news.vue';
	

	export default {
		name:'My',
		data(){
			return {
				userId:null
			}
		},
		methods:{
			goLogin:function(){
                window.location.href='/Login'
            }
		},
		components:{
			
			
			

		},
		mounted(){
			
            var user_id = sessionStorage.userId;
                if(user_id != null){
                    this.userId = user_id;    
                }
                


		}




	}
</script>

<style type="text/css" >
	a{color: inherit}
    .btnlogin { font-size:14px; color:#0dc751; background:#fff; border-radius:5px;padding:5px 10px; text-align:center;}
</style>


