<!-- 集市下面的三大主粮组件 -->
<template>
    <div id="SanDaZhuLiangCont">
        <!-- <v-headr :myname="myname"></v-headr> -->
        <header class="head-container title-container header_title" >
            <div class="aux_back"  @click="backPrv()"></div>
            <p class="message-title text-cent">{{titleText}}</p>    
        </header>

            <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10"  class="wrip-con">
                <div class="aui-card-list">
                        <a  href="javascript:void(0);"
                           class="aui-panel-cell"
                           v-for="key in datas"> 
                            <div class="aui-panel-cell-bd">
                                <h4>{{key.HxCropofferStr}}</h4>
                                <span>{{key.OfferDate}}</span>
                            </div> 
                            <div class="float-rigt">
                                <em class="jiage">￥{{key.Price}}元</em>
                            </div>
                        </a>
                </div>
            </div>
            <img src="../../assets/img/Loading.gif" class="loading" v-if="loading">
      
    </div>
</template>
<style scoped>
    #Headr{ height: 44px; }
    .loading{
        display: block;
        margin: 0 auto;
    }
    .wrip-con{
        margin-top: 44px;
    }
</style>
<script type="text/javascript">
    import Headr from '../../components/header.vue'
    export default {
        name:'SanDaZhuLiang_Cont',
        data(){
            return { 
                isA:0,
                msg:'集市下面的三大主粮组件',
                myname:{
                    name:'三大主粮333',
                    IsSubPage:true
                },
                titleText:'',
                datas:[],
                busy:false,
                loading:true,
                PerPageNum:20,
                PageNum:0,
                
            }
        },
        components:{
           /* 'v-headr':Headr,*/
        },
        methods:{
            loadMore() {
                //alert("++")
                 this.busy = true;
                 this.getLIst(true);
                  setTimeout(() => {
                    //console.log(this.PageNum)
                    this.PageNum++;
                   // console.log(this.PageNum)
                    this.busy = false;
                  }, 3000);
            },

             loadList:function(){
                 //
             },
             /*获取地区*/
            getRegion(){
                this.axios.get('/api/flxoa/Region',{
                    params:{
                        RegionID:0,
                    }
                }).then(res => {
                    console.log("获取地区成功"+res)
                    //let data = res.data[0].data
                    //this.daogu = data
                    console.log(JSON.stringify(res))
                    //console.log(res.data[0].data[0])
                  
                },err => {
                     console.log("获取地区失败"+err)
                })
                .catch(err=> {
                    console.log("捕获的错误")
                })
             },
            getLIst(flag){
                 //this.$indicator.open();
                let qStr = this.$route.query.q;
                console.log("接受"+qStr)
                if(qStr == '0328'){
                    this.titleText = "豆油"
                }else if(qStr == '0438'){
                     this.titleText = "进口大米"
                }else if(qStr == '0107'){
                     this.titleText = "热门地区-玉米"
                }else if(qStr == '0209'){
                     this.titleText = "地区报价-小麦"
                }else if(qStr == '0212'){
                     this.titleText = "优质小麦"
                }else if(qStr == '0222'){
                     this.titleText = "进口小麦"
                }else if(qStr == '0564'){
                     this.titleText = "热门品种"
                }else if(qStr == '0435'){
                     this.titleText = "豆油详情"
                }else if(qStr == '0544'){
                     this.titleText = "豆粕详情"
                }else if(qStr == '0471'){
                     this.titleText = "稻米"
                }
                 console.log("==========="+qStr)
                
                
                this.axios.get('/api/flxoa/CropOffer',{
                   params:{
                        PerPageNum:this.PerPageNum,
                        PageNum:this.PageNum,
                        CropCategoryCode:qStr,
                        iscropcategory:1,

                    }

                }).then(res => {
                    console.log("三大主粮更多成功"+res)
                    console.log(res.data[0].data)
                    //this.$indicator.close();
                    
                    if(flag){
                         this.datas =  this.datas.concat(res.data[0].data)
                         if(res.data[0].data.length < this.PerPageNum){
                            this.busy = true
                            this.loading = false
                         }else{
                            this.busy = false
                         }
                    }else{
                        this.datas = res.data[0].data
                        this.busy = false
                    }

                },err => {
                     console.log("三大主粮更多失败"+err)
                })
                .catch(err=> {
                    console.log("捕获的错误")
                })
               
              // console.log(this.datas)
               
             },
             goInfo:function(){

             },
             goMore:function(q){
                console.log(q);
             },
             tabToggle:function(index){
                 
             },
             backPrv(){
                this.$router.go(-1)
              }

            




        },
        mounted(){  
           // this.getLIst();
            //this.getRegion();

       










        }
    }
</script>


