<!-- 集市下面的三大主粮组件 -->
<template>
	<div id="SanDaZhuLiangXQ">
        <header class="head-container title-container header_title" >
            <div class="aux_back"  @click="backPrv()"></div>
            <p class="message-title text-cent">{{myname}}</p>
            <span class="addAttention" @click="addAttention()">+</span>   
        </header>
        

            <div class="price-dispcard-warp" style="margin-top: 44px;">
                <ul class="price-dispcard clearfix">
                    <li class="price-dispcard-item bord-b today-price float-lft text-cent">
                        <p class="price-dispcard-num">{{jinrijiage}}</p>
                        <p class="price-dispcard-text">今日价格</p>
                    </li>
                    <li class="price-dispcard-item bord-b bord-lft yesterday-price float-rigt text-cent">
                        <p class="price-dispcard-num">{{zuorijiage}}</p>
                        <p class="price-dispcard-text">昨日价格</p>
                    </li>
                    <li class="price-dispcard-item bord-b lastWwk-price float-lft text-cent">
                        <p class="price-dispcard-num">{{shangzhoujunj}}</p>
                        <p class="price-dispcard-text">上周均价</p>
                    </li>
                    <li class="price-dispcard-item bord-b bord-lft lastWwk-rise float-rigt text-cent">
                       <!--  <p class="price-dispcard-num lastWwk-rise-up"> -->
                        <span class="price-dispcard-num lastWwk-rise-up" v-if="shangzhouzhangfy >= 0"><span class="up">+</span><em class="price-num">{{shangzhouzhangfy}}</em><span>%</span></span>
                        <span class="price-dispcard-num lastWwk-rise-up" v-if="shangzhouzhangfy < 0"><span class="dow col-green">-</span><em class="price-num col-green">{{shangzhouzhangfy}}</em><span class="col-green">%</span></span>
                       <!--  </p> -->
                        <p class="price-dispcard-text">当日涨跌</p>
                    </li>
                </ul>
            </div>

        <div class="bazaar-title">
            <div class="act-bazaar-title-det quotation">{{myname}}</div>
        </div>

        <div class="trend-chart-con">
            <ul class="trend-num-wrap clearfix">
                <li class="trend-num-item float-lft text-cent">
                    <p class="price-dispcard-num">{{benzhouzuixin}}</p>
                    <p class="price-dispcard-text">本周最新价格</p>
                </li>
                <li class="trend-num-item trend-num-bar float-lft"></li>
                <li class="trend-num-item float-rigt text-cent">
                    
                  <span class="price-dispcard-num lastWwk-rise-up" v-if="jwrzhangfu >= 0">+{{jwrzhangfu}}%</span>
                  <span class="price-dispcard-num col-green" v-if="jwrzhangfu < 0">-{{jwrzhangfu}}%</span>
                   
                    <p class="price-dispcard-text">近五日涨跌</p>
                </li>
            </ul>
            <div class="trend-chart">
                <div id="chart"  style="height: 200px; padding-bottom: 20px" >
                         图表
                 </div>
            </div>
        </div>

         



	</div>
</template>
<style scoped>
    #Headr{ height: 44px; }
    .addAttention{
      color: #eee;
      font-size: 30px;
      position: absolute;
      top: 0;
      right: 10px;

    }
</style>
<script type="text/javascript">
import { Toast } from 'mint-ui'
    let echarts = require('echarts/lib/echarts')
    require('echarts/lib/chart/line')
    // 引入提示框和title组件
    require('echarts/lib/component/tooltip')
    require('echarts/lib/component/title')
	export default {
		name:'SanDaZhuLiang_XQ',
		data(){
			return { 
                isA:0,
				        msg:'集市下面的三大主粮组件',
                myname:'',
                jinrijiage:0,
                zuorijiage:0,
                shangzhoujunj:0,
                shangzhouzhangfy:0,
                benzhouzuixin:0,
                jwrzhangfu:0,
                datas:null,
                OfferDateStart:'',
                OfferDateEnd:'',               
                option:{
                        xAxis: {
                            type: 'category',
                            axisLine: {
                                lineStyle: {
                                    color: '#e4e4e4'
                                }
                            },
                            axisLabel:{
                                textStyle: {
                                    color: '#7b7d7f'
                                }
                            },
                            data: []
                        },
                        yAxis: {
                            type: 'value',
                            color:'#7b7d7f',
                            axisLine:{       //y轴
                              show:false,
                            },
                            axisTick:{       //y轴刻度线
                              show:false
                            },
                            axisLabel:{
                                textStyle: {
                                    color: '#7b7d7f'
                                }
                            },
                            splitLine: {
                               lineStyle: {
                                    color: '#e4e4e4'
                                }
                            },
                            min:0,

                        },
                        grid:{
                            x:50,
                            y:20,
                            x2:5,
                            y2:20
                        },
                        tooltip: {
                            trigger: 'axis'
                        },
                        series: [{
                            itemStyle: {
                                normal: {
                                    color: "#fe7f1e"
                                }
                            },
                            lineStyle: {
                                normal: {
                                    width: 3,
                                    shadowColor: '#ffdcd0',
                                    shadowBlur: 3,
                                    shadowOffsetY: 6
                                }
                            },
                            areaStyle: {
                                    normal: {
                                        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                            offset: 0,
                                            color: '#fff2ee'
                                        }, {
                                            offset: 1,
                                            color: '#ffffff'
                                        }])
                                    }
                            },
                            showSymbol: false,
                            data: [],
                            type: 'line',
                            smooth: true
                        }]
                },
               
			      



            }
		},
        components:{
           /* 'v-headr':Headr,*/
        },
		methods:{
            /*抽取图标所需的数据*/
            extractChartData(obj,target){
                let Arr = [];
                    for(let i=0; i<obj.length; i++){
                        Object.keys(obj[i]).forEach(function(key,value){
                            if(key == target){
                                Arr.push(obj[i][key])
                            }
                           // console.log(key,obj[i][key]);
                        });
                    }
                //console.log(Arr)
                return Arr
            },
            /*截取X轴日期*/
            makeDateX(obj){
                let dateX = []
               for(let i=0; i<obj.length; i++){
                    dateX.push(obj[i].substring(5).replace("-","/"))
                   //console.log(obj[i].substring(5).replace("-","/")) 
                  // console.log(obj[i].slice(0,5))
               }
               return dateX
            },
            /*取数组最小值*/
            getMix (obj){
                let mixArr =  Math.min.apply(null, obj);
                return mixArr
            },

            backPrv(){
                this.$router.go(-1)
            },
            loadList:function(){
               //
            },
             /*获取当前日期*/
            getNowFormatDate() {
                  var date = new Date();
                  var seperator1 = "-";
                  var year = date.getFullYear();
                  var month = date.getMonth() + 1;
                  var strDate = date.getDate();
                  if (month >= 1 && month <= 9) {
                      month = "0" + month;
                  }
                  if (strDate >= 0 && strDate <= 9) {
                      strDate = "0" + strDate;
                  }
                  var currentdate = year + seperator1 + month + seperator1 + strDate;
                  return currentdate;
            },
             /*获取前几日日期*/
            getBeforeDate(n){
                 var n = n;
                 var d = new Date();
                 var year = d.getFullYear();
                 var mon=d.getMonth()+1;
                 var day=d.getDate();
                 if(day <= n){
                     if(mon>1) {
                          mon=mon-1;
                     } else {
                          year = year-1;
                          mon = 12;
                     }
                 }

                   d.setDate(d.getDate()-n);
                   year = d.getFullYear();
                   mon=d.getMonth()+1;
                   day=d.getDate();
                 /*var s = year+"-"+(mon<10?('0'+mon):mon)+"-"+(day<10?('0'+day):day);*/
                 var s = year+"-"+mon+"-"+day;
                return s;
            },

             /*获取五日数据*/
            getFiveDate(){
                //this.$indicator.open()
                let Region = this.$route.query.Region;
                let CropCategory = this.$route.query.CropCategory;
                console.log(Region)
                console.log(CropCategory)
                this.axios.get('/api/flxoa/CropOffer',{ 
                    params:{
                       RegionCode:Region,   //地区类别
                       CropCategoryCode:CropCategory,  //作物品类
                       OfferDateStart:this.OfferDateStart,
                       OfferDateEnd:this.OfferDateEnd,
                       iscropcategory:1,
                    }
                })
                .then(res => {
                    console.log("三大主粮详情数据成功"+res)
                    console.log("三大主粮的详情"+JSON.stringify(res))
                    let obj = res.data[0].data[0]
                     if(res.data[0].data[0].todayTrend == null){
                        res.data[0].data[0].todayTrend = 0
                     }
                     if(res.data[0].data[0].fiveDayTrend == null){
                        res.data[0].data[0].fiveDayTrend = 0
                     }
                    this.myname = res.data[0].data[0].HxCropofferStr
                    this.jinrijiage = res.data[0].data[0].Price
                    this.zuorijiage = res.data[0].data[1].Price
                    this.shangzhoujunj = res.data[0].data[0].Price
                    this.benzhouzuixin = res.data[0].data[0].Price
                    this.shangzhouzhangfy = res.data[0].data[0].todayTrend
                    this.jwrzhangfu = res.data[0].data[0].fiveDayTrend
                    //this.$indicator.close()
                    this.option.xAxis.data = this.makeDateX(this.extractChartData(res.data[0].data,'OfferDate'))
                    this.option.series[0].data = this.extractChartData(res.data[0].data,"Price")
                    //console.log(this.option.series.data)
                    this.option.yAxis.min = this.getMix(this.extractChartData(res.data[0].data,"Price")) 
                    this.draMarketDate('chart',this.option)
                   
                },err =>{
                    console.log("三大主粮详情数据失败"+res)
                })
                .catch(err =>{
                     console.log("捕获的错误")
                })

                

            },

			      goInfo:function(){

            },
             goMore:function(n){
                console.log(n);
             },
             tabToggle:function(index){
                 
             },
             draMarketDate(domId,option){
              //alert(222)
                let myChart = echarts.init(document.getElementById(domId))
                // 绘制图表
                myChart.setOption(option);
            },
            addAttention(){
               let Region = this.$route.query.Region;
                let CropCategory = this.$route.query.CropCategory;
                //Subscribe
                this.axios.get('/api/flxoa/SubscribeAdd?RegionCode='+Region+'&CropCategoryCode='+CropCategory).then(function(response){ 
          console.log('--------------------订阅---------------------')
          console.dir(response);
              console.log('订阅成功');Toast("订阅成功");
              });  
            }








		},
		mounted(){  
            this.OfferDateStart = this.getBeforeDate(6)
            this.OfferDateEnd = this.getNowFormatDate() 
            this.getFiveDate()
            
            
		},
    created(){
      // this.getFiveDate()
    },
	}
</script>


