                                break;
                            case 1:
                                this.getDataCereal("0102")
                                 this.getPrediction("01")
                                this.title_sml_1=false;
                                this.title_sml_2=true;
                                this.title_sml_3=false;
                                this.title_sml_4=true;
                                this.title_sml_5=false;
                                this.title_sml_6=false;
                                this.title_sml_7=true;
                                this.title_sml_8=false;
                                this.title_sml_9=false;
                                this.title_sml_10=false;
                                this.title_sml_11=true;
                                this.title_sml_12=false;
                                this.title_sml_13=false;
                                this.draMarketDate('chartYuMi',this.optionYuMi)

                                break;
                            case 2:
                                this.getDataCereal("0209") //地区报价
                               
                                //this.getDataCerealDetallQuality('0223')
                                 this.getDataCerealDetall('0212')//优质
                                 this.getPrediction("02")
                                this.title_sml_1=false;
                                this.title_sml_2=false;
                                this.title_sml_3=false;
                                this.title_sml_4=false;
                                this.title_sml_5=true;
                                this.title_sml_6=true;
                                this.title_sml_7=false;
                                this.title_sml_8=false;
                                this.title_sml_9=true;
                                this.title_sml_10=false;
                                this.title_sml_11=false;
                                this.title_sml_12=true;
                                this.title_sml_13=true;
                                this.draMarketDate('chartXiaoMai',this.optionXiaoMai)
                                break;
                        }
             },
             draMarketDate(domId,option){
                let myChart = echarts.init(document.getElementById(domId))
              // 绘制图表
              myChart.setOption(option);
            },
        },
        mounted(){
           this.tabToggle(0); //初始化加载 
           this.getChartData();
           this.getChartDataYuMi();
           this.getChartDataXiaoMai();
           //this.draMarketDate('chartYuMi',this.optionYuMi)
           //this.draMarketDate('chartDaoMi',this.optionDaoMi)
           //this.draMarketDate('chartXiaoMai',this.optionXiaoMai)

 //this.getDataCereal('0328')


        }
    }
</script>


