<!-- 个人订阅 -->
    <template>
        <div id="GeRenDingYue">
            <v-headr :myname="myname"></v-headr>
            <div class="bod-list-container">
                <ul class="list-warp" v-for="(item,key) in DingYueShuJu">
                     <mt-cell-swipe
                          :right="[
                            {
                              content: '删除',
                              style: { background: 'red', color: '#fff' },
                              handler: function(){ delVideo(item)}
                            }
                          ]">


                            <li class="list-con clearfix">
                                
                                <div class="list-num clearfix ">
                                    <p class="bod-list-title">{{item.RegionName}}{{item.CropCategoryName}}</p>

                                    <span class="list-num-percentage float-lft"></span>

                                    <p class="list-title-text">{{item.OfferDate}}</p>
 
                                    <span  class="list-num-money float-rigt">
                                        <i class="symbolNum">{{item.Price}}{{item.UnitName}}</i>
                                        <small class="symbolTop" v-if="item.TodayTrend > 0" >{{item.TodayTrend}}%</small>
                                        <small class="symbolBottom" v-if="item.TodayTrend < 0">{{item.TodayTrend}}%</small>
                                        
                                     </span> 
                                </div>
                           
                             </li>

                        </mt-cell-swipe>
                    
                    
                </ul>
                
                
            </div>
            
        </div>
    </template>

    <script type="text/javascript">
    import Headr from '../../components/header.vue' 
     const DingYueJS = JSON.parse(sessionStorage.dingyue);
    console.log(DingYueJS.result[0].JiShi)
    export default {
        name:'GeRenDingYue',
        data(){
            return {
                myname:{
                    name:'个人订阅',
                    IsSubPage:true
                },
                DingYueShuJu:null

                
                
            }
        },
        components:{
            'v-headr':Headr,
        },
        methods:{
            getDate(){
                // this.DingYueShuJu = DingYueJS.result[0].JiShi
                // console.log(this.DingYueShuJu)
                var scThis = this; 
                var user_id = sessionStorage.userId; 
                if(user_id==null){
                    console.log("未登录")
                }
                this.axios.get('/api/flxoa/Subscribe?PersonalSubscriptions=1').then(function(response){ 
                    console.log('--------------------订阅---------------------')
                    console.dir(response);
                    scThis.DingYueShuJu = response.data[0].data;
                });  
            },
           delVideo(keys) {
                console.log(keys)  
                var scThis = this;  
                this.axios.get('/api/flxoa/SubscribeDel?SourceCode='+keys.SubscribeID).then(function(response){ 
                    console.log('--------------------删除-个人-订阅---------------------')
                    console.dir(response.data[0].data);
                    this.$toast({message: '删除成功',duration: 2000});
                    this.getDate()
                });   
               
             
            },
            
           


        },
        mounted(){
            this.getDate() 


        }

    }
</script>
<style type="text/css">
    .mint-cell-title {
            -webkit-box-flex: 0;
            -ms-flex: 0;
            flex: 1;
            flex: 0;
    }
    .mint-cell-value {
        color: #888;
        /* display: -webkit-box; */
        display: -ms-flexbox;
        /* display: flex; */
        /* -webkit-box-align: center; */
        -ms-flex-align: center;
        align-items: center;
        width: 100%;
    }
    .mint-cell-value {
        color: #888;
        /* display: -webkit-box; */
        display: -ms-flexbox;
        /* display: flex; */
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        display: inline-block;
    }
    .list-warp li.list-con {
        border-bottom: 0; 
       
    }
    
    .mint-cell-swipe-button {
        
        line-height: 64px;
    }
    .list-title-text{ float:left;}
    .list-con { width:100%;}

    a.mint-cell{
        padding: 0 15px 0 0;
    }
    a.mint-cell-swipe-button{
        line-height: 52px
    }
    .symbolTop { color:red; text-align:right; display:block;}
    .symbolBottom {  color: green; text-align:right; display:block;}
    .symbolNum {  display:block;}

</style>


 


