<!-- 资讯页内的关注项组件 -->
<template>
	<div id="Concern">
		<div class="bod-container">
	        <ul class="bod-warp">
	            <li class="bod-list clearfix" 
	            	v-for="item in datas">
	                <!-- <div class="bod-list-img float-lft">
	                    <img class="list-img width100 " src="../../assets/img/list-img.png"/>
	                </div>  -->               
	                <div class="bod-list-info float-rigt">
	                    <div class="concern-wrap">
	                        <span class="concern-type displ-inl-blk">{{item.NewsCategoryName}}</span>
	                        <p class="bod-list-title-concern displ-inl-blk">{{item.Title}}</p>
	                    </div>
	                   <ul class="source-wrap clearfix">
	                        <li class="float-lft source-text">来源：<span>{{item.SourceName}}</span></li>
	                        <li class="float-rigt concern text-cent"  
	                        :class="{'guanzhu':item.isFollow}"
	                        @click="goFollow(item,item.isFollow)">{{ item.isFollow==0?'关注':'已关注' }}</li>
	                    </ul>
	                    <ul class="time-wrap clearfix">
	                        <li class="float-lft">{{item.PublishDt}}</li>
	                        <!-- <li class="float-rigt text-rigt message"><img class="message-ico" src="../assets/img/message.png" />412</li> -->
	                        <li class="float-rigt text-rigt check">
	                        	<!-- <img class="check-ico" src="../../../assets/img/check.png" />1108 -->
	                        </li>
	                    </ul>
	                </div>
	            </li>
	            
	        </ul>
        
    	</div>
		 
	</div>
</template>
<style type="text/css" scoped>
	.bod-list .bod-list-info { width: 100%; }
	.concern{ width: 38px; }
	.guanzhu {     border: 1px solid #7b7d7f;color: #6a6a6a; }
	.nav-wrap { border-bottom: 1px solid #ddd; }
	.source-wrap { margin:0; }
</style>
<script type="text/javascript">
const dataSrc = JSON.parse(sessionStorage.datas) //数据源
	console.log(dataSrc);
	export default {
		name:'Concern',
		data(){
			return {
				datas:null
			}
		},
		methods:{
			goDetail:function(){
				console.log("去详细页");
			},
			goFollow:function(items,isFollow){
				console.log(isFollow);
				if(isFollow==0){
					this.followPost(items.SourceCode);
				}else{
					this.followRemove(items.Followid);
				}
				
			},
			init_guanZhu:function(){
				var scThis = this; 
				var user_id = sessionStorage.userId; 
				if(user_id == null) user_id = '000000';
				this.axios.get('/api/flxoa/News?tokenid='+user_id+'&Type=1').then(function(response){ 
					console.log('--------------------收藏---------------------')
					console.dir(response.data[0].data);
	                scThis.datas = response.data[0].data;
	            });  
			}, 
			followPost:function(sorcId){ console.log("关注"); var this_ = this;
				var user_id = sessionStorage.userId;
				if(user_id == null) user_id = '000000';
				this.axios.get('/api/flxoa/FollowAdd?SourceCode='+sorcId)
		          .then(function (response) {
		        	this_.init_guanZhu()
		        	console.log('关注成功');Toast("关注成功");
		            console.log(response);  
		          })
		          .catch(function (error) {
		              console.log(error);
		          }); 
			},
			followRemove:function(sorcId){ console.log("取消关注");var this_ = this;
				var user_id = sessionStorage.userId;
				if(user_id == null) user_id = '000000';
				this.axios.get('/api/flxoa/FollowDel?SourceCode='+sorcId)
		          .then(function (response) {
		        	this_.init_guanZhu()
		        	console.log('取消关注成功');Toast("取消关注成功");
		            console.log(response);  
		          })
		          .catch(function (error) {
		              console.log(error);
		          }); 
			}
		},
		components:{
			
			

		},
		mounted(){
			 this.init_guanZhu();
		}

	}
</script>


