		methods:{
			/*获取当前时间*/
		    getNowFormatDate() {
		        var date = new Date();
		        var seperator1 = "-";
		        var year = date.getFullYear();
		        var month = date.getMonth() + 1;
		        var strDate = date.getDate();
		        if (month >= 1 && month <= 9) {
		            month = "0" + month;
		        }
		        if (strDate >= 0 && strDate <= 9) {
		            strDate = "0" + strDate;
		        }
		        var currentdate = year + seperator1 + month + seperator1 + strDate;
		        return currentdate;
			},
			/*上个月时间*/
			makeLastMonthDate(){
				var nowdate = new Date();
			    //获取系统前一个月的时间
			    nowdate.setMonth(nowdate.getMonth()-1);
			    var y = nowdate.getFullYear();
			    var m = nowdate.getMonth()+1;
			    var d = nowdate.getDate();
			    var formatwdate = y+'-'+m+'-'+d;
			    return formatwdate			   
			},
			/*取前一年的日期*/
			makeLastYear(n){
				let oneYear;
				let y = this.getNowFormatDate().substring(0,4)
				let oth = this.getNowFormatDate().substring(4)
					y = y-n
				//console.log(y)
				//console.log(oth)
				 return oneYear = y+""+oth
			},
			/*抽取图标所需的数据*/
            extractChartData(obj,target){
                let Arr = [];
                    for(let i=0; i<obj.length; i++){
                        Object.keys(obj[i]).forEach(function(key,value){
                            if(key == target){
                                Arr.push(obj[i][key])
                            }
                           // console.log(key,obj[i][key]);
                        });
                    }
                //console.log(Arr)
                return Arr
            },

            extractChartData2(obj,target){
            	const charArr = obj.data[0].data; 
            	var r = charArr.filter(function (element, index, self) { 
            		if(element.RegionCode == target) 
					return element; 
				});	 
				 //console.log(r) 
				 return r
            },
            /*截取X轴日期*/
            makeDateX(obj){
                let dateX = []
               for(let i=0; i<obj.length; i++){
                    dateX.push(obj[i].substring(5).replace("-","/"))
                   //console.log(obj[i].substring(5).replace("-","/")) 
                  // console.log(obj[i].slice(0,5))
               }
               return dateX
            },
            /*取数组最小值*/
            getMix (obj){
                let mixArr =  Math.min.apply(null, obj);
                return mixArr 
            }, 
			/*获取图表数据*/
            getChartData(timeQuantum){ 
            	if(timeQuantum == "0"){
            		//alert("月")
            		this.yiy = true,
				    this.yin = false,
            		this.liangn = false,	
            		this.OfferDateStart = this.makeLastMonthDate()
					this.OfferDateEnd = this.getNowFormatDate()
            	}else if(timeQuantum == "1"){
            		//alert("一年")
            		this.liangn = false,
				    this.yiy = false,
				    this.yin = true,
            		//this.OfferDateEnd = this.getNowFormatDate()
            		this.drawLine('chartyn',this.optionyn);
            	}else if(timeQuantum == "2"){
            		//alert("二年")
            		this.yiy = false,
				    this.yin = false,
            		this.liangn = true,				   
            		//this.OfferDateEnd = this.getNowFormatDate()
            		this.drawLine('chartln',this.optionln);
            	}
            	
            	let params = {
                        regionList:'0153,0083,0113',
                        CropCategoryCode:'0544',
                        OfferDateStart:this.OfferDateStart,
                        OfferDateEnd:this.OfferDateEnd,
                    }           
                this.axios.get('/api/flxoa/CropOfferIcon',{
                	params:params,
                })
                .then(res => { 
                	let data =  res.data[0].data;
                	let RegArr1 = this.extractChartData2(res,'0153')
                	let RegArr2 = this.extractChartData2(res,'0083')
                	let RegArr3 = this.extractChartData2(res,'0113')
                	let mixCon = []
                	//console.log(RegArr1)
                	/*取X轴日期*/
                	this.option.xAxis.data = this.makeDateX(this.extractChartData(RegArr1,"OfferDate")) 
                	this.option.series[0].data = this.extractChartData(RegArr1,'Price')
                	this.option.series[1].data = this.extractChartData(RegArr2,'Price')
                	this.option.series[2].data = this.extractChartData(RegArr3,'Price')
                	mixCon.push(this.getMix(this.extractChartData(RegArr1,'Price')))
                	mixCon.push(this.getMix(this.extractChartData(RegArr2,'Price')))
                	mixCon.push(this.getMix(this.extractChartData(RegArr3,'Price')))
                	this.option.yAxis.min = this.getMix(mixCon)
                	this.option.legend.data[0] = RegArr1[0].RegionName
                	this.option.legend.data[1] = RegArr2[0].RegionName
                	this.option.legend.data[2] = RegArr3[0].RegionName
                	this.option.series[0].name = RegArr1[0].RegionName 
					this.option.series[1].name = RegArr2[0].RegionName
					this.option.series[2].name = RegArr3[0].RegionName

 					this.drawLine('chart',this.option);
                })

            },


            


			drawLine(domId,option) {
		      // 基于准备好的dom，初始化echarts实例
		      let myChart = echarts.init(document.getElementById(domId))
		      myChart.setOption(option);
		      setTimeout(function (){
				    window.onresize = function () {
				        myChart.resize();
				        //alert(666)
				        
				    }
			  },2000)
				
		    },







