<!-- 大的资讯组件 -->
<template>
	<div id="Index">
		<v-headr :myname="myname"></v-headr>
		<div class="bazaar-hed-bg">
			<div class="endways"></div>
			<div style="width: 49%; float: left;">
				<div class="swiper-container" >
		            <div class="swiper-wrapper" >
					    <div class="swiper-slide" v-for="(item,key) in bannerListLeft">
					        <ul class="bazaar-hed-quote">
					            <li class="bazzar-had-item text-cent">
					                <p class="hed-quote-lft-num">￥<i>{{item[7]}}</i></p>
					                <p class="hed-quote-lft-text">{{item[5]}}</p>
					            </li>
					        </ul>
					    </div>

				    </div>
				</div>
			</div>
			<div style="width: 49%;  float: right;">
				<div class="swiper-container">
		            <div class="swiper-wrapper">
					    <div class="swiper-slide" v-for="(item,key) in bannerListRight">
					        <ul class="bazaar-hed-quote ">
					            <li class="bazzar-had-item text-cent">
					                <p class="hed-quote-lft-num">￥<i>{{item[7]}}</i></p>
					                <p class="hed-quote-lft-text">{{item[5]}}</p>
					            </li>
					        </ul>
					    </div>
				    </div>
				</div>
			</div>
	    </div>




	    <div class="bazaar-sudoku-con">
	        <ul class="bazaar-sudoku-warp">
	            <li> 
	            	<router-link :to="{path:'/JiShi/HuoYueShiChang',params:{username:'jspang'},query:{name:'AAA'}}">
	            		<a href="javascript:;"><img src="../../assets/img/bazaar/bazaar-01.png" class="sudoku-img" /><p class="sudoku-act-bazaar ">活跃市场</p></a>
            		</router-link>
	            </li>
	            <li> 
	            	<router-link :to="{path:'/JiShi/SanDaZhuLiang'}">
	            		<a href="javascript:;"><img src="../../assets/img/bazaar/bazaar-02.png" class="sudoku-img" /><p class="sudoku-act-food ">三大主粮</p></a>
	            	</router-link>
	            </li>
	            <li> 
	            	<router-link :to="{path:'/JiShi/YouZhiYouLiao'}">
	            		<a href="javascript:;"><img src="../../assets/img/bazaar/bazaar-03.png" class="sudoku-img" /><p class="sudoku-act-grease ">油脂油料</p></a>
            		</router-link>
	            </li>
	            <li>
	            	<router-link :to="{path:'/JiShi/YingYangSiLiao'}"> 
	            		<a href="javascript:;"><img src="../../assets/img/bazaar/bazaar-04.png" class="sudoku-img" /><p class="sudoku-act-fodder ">营养饲料</p></a>
	            	</router-link>
	            </li>
	            <li> 
	            	<router-link :to="{path:'/JiShi/QiYeZhiGong'}">
	            		<a href="javascript:;"><img src="../../assets/img/bazaar/bazaar-05.png" class="sudoku-img" /><p class="sudoku-act-supply ">企业直供 </p></a>
            		</router-link>
            	</li>
	            <li>
	            	<router-link :to="{path:'/JiShi/TuJieShiChang'}"> 
	            		<a href="javascript:;"><img src="../../assets/img/bazaar/bazaar-06.png" class="sudoku-img" /><p class="sudoku-act-mapbazaar ">图解市场</p></a>
            		</router-link>
	            </li>
	            <li>
	            	<router-link :to="{path:'/JiShi/ZongHeChaXun'}"> 
	            		<img src="../../assets/img/bazaar/bazaar-07.png" class="sudoku-img" /><p class="sudoku-act-check ">综合查询</p> 
            		</router-link>
	            </li>
	            <li>
	            	<router-link :to="{path:'/JiShi/GeRenDingYue'}">
	            		<a href="javascript:;"><img src="../../assets/img/bazaar/bazaar-08.png" class="sudoku-img" /><p class="sudoku-act-subscription ">我的订阅</p></a>
            		</router-link>
            	</li>
	        </ul>
	    </div>


	    <div class="bazaar-title clerafix">
	        <div class="act-bazaar-title-det clerafix">活跃市场
	            <p class="act-bazaar-title-type float-rigt">豆油</p>
	        </div>
	    </div>

	    <div class="bazaar-trend-chart-con">
	        <ul class="bazaar-trend-num-wrap clearfix">
	            <li class="bazaar-trend-num-item float-lft text-cent">
	                <p class="price-dispcard-num">{{soyaBeanOilNum}}</p>
	                <p class="price-dispcard-text">最新价格(吨/元)</p>
	            </li>
	            <li class="trend-num-item trend-num-bar float-lft"></li>
	            <li class="bazaar-trend-num-item float-lft text-cent">
	                <p class="price-dispcard-num lastWwk-rise-up" v-if="soyaBeanOilUpDow >= 0">
	                    <span class="up">+</span><em class="price-num">{{soyaBeanOilUpDow}}</em><span>%</span><span class="up-arrow"></span>
	                </p>
	                <p class="price-dispcard-num lastWwk-rise-dow" v-if="soyaBeanOilUpDow < 0">
	                    <span class="dow">-</span><em class="price-num">{{soyaBeanOilUpDow}}</em><span>%</span><span class="dow-arrow"></span>
	                </p>
	                <p class="price-dispcard-text">近五日涨幅(吨/元)</p>
	            </li>
	             <!-- <router-link to="/JiShi/HuoYueShiChangDouYou" class=""> -->
	            	<a href="javascript:;" class="details-btn" @click="goDetail('0328')">详情</a>
            	 <!-- </router-link> -->
	        </ul>
	        <div class="trend-chart" id="chart">
	            
	        </div>
	    </div>

	    <div class="bazaar-title clerafix">
	        <div class="act-bazaar-title-det clerafix">活跃市场
	            <p class="act-bazaar-title-type float-rigt">豆粕</p>
	        </div>
	        <div class="bazaar-trend-chart-con">
	        <ul class="bazaar-trend-num-wrap clearfix">
	            <li class="bazaar-trend-num-item float-lft text-cent">
	                <p class="price-dispcard-num">{{beanPulpNum}}</p>
	                <p class="price-dispcard-text">最新价格(吨/元)</p>
	            </li>
	            <li class="trend-num-item trend-num-bar float-lft"></li>
	            <li class="bazaar-trend-num-item float-lft text-cent">
	                 <p class="price-dispcard-num lastWwk-rise-up" >
	                    <span class="up">+</span><em class="price-num" v-if="beanPulpNumUpDow >= 0">{{beanPulpNumUpDow}}</em><span>%</span><span class="up-arrow"></span>
	                </p>
	                <p class="price-dispcard-num lastWwk-rise-dow" v-if="beanPulpNumUpDow < 0">
	                    <span class="dow">-</span><em class="price-num">{{beanPulpNumUpDow}}</em><span>%</span><span class="dow-arrow"></span>
	                </p>
	                <p class="price-dispcard-text">近五日涨幅(吨/元)</p>
	            </li>
	            	<a href="javascript:;" class="details-btn" @click="goDetail('0544')">详情</a>
	            
	        </ul>
	        <div class="trend-chart" id="chartDouPo">
	           
	        </div>
	    </div>
	    </div>
    	
	</div>
</template>

<script type="text/javascript">
	import Headr from '../../components/header.vue'
	import Swiper from 'swiper' 
	import 'swiper/dist/css/swiper.min.css';
	let echarts = require('echarts/lib/echarts')
	require('echarts/lib/chart/line')
	// 引入提示框和title组件
	require('echarts/lib/component/tooltip')
	require('echarts/lib/component/title')

	export default {
		name:'JiShi',
		data(){
			return {
				myname:{
					name:'集市',
				},
				bannerListLeft:[],
				bannerListRight:[],
				/*豆油,豆粕涨幅数据*/
				soyaBeanOilNum:0,
				soyaBeanOilUpDow:0,
				beanPulpNum:0,
				beanPulpNumUpDow:0, 



				/*豆油*/
				optionDouYou:{
					xAxis: {
						type: 'category',
						axisLine: {
	                        lineStyle: {
	                            color: '#e4e4e4'
	                        }
	                    },
	                    axisLabel:{
	                        textStyle: {
	                            color: '#7b7d7f'
	                        }
	                    },
						data: []
					},
					yAxis: {
						type: 'value',
						color:'#7b7d7f',
						axisLine:{       //y轴
	                      show:false,
	                    },
	                    axisTick:{       //y轴刻度线
	                      show:false
	                    },
	                    axisLabel:{
	                        textStyle: {
	                            color: '#7b7d7f'
	                        }
	                    },
	                    splitLine: {
	                       lineStyle: {
	                            color: '#e4e4e4'
	                        }
	                    },
	                    min:0,

					},
					grid:{
	                    x:50,
	                    y:20,
	                    x2:5,
	                    y2:20
	                },
					tooltip: {
						trigger: 'axis'
					},
					series: [{
						itemStyle: {
	                        normal: {
	                            color: "#1cbc2d"
	                        }
	                    },
						lineStyle: {
							normal: {
								width: 3,
								shadowColor: '#c4e7c2',
								shadowBlur: 3,
								shadowOffsetY: 6
							}
						},
						areaStyle: {
								normal: {
									color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
										offset: 0,
										color: '#def2dd'
									}, {
										offset: 1,
										color: '#ffffff'
									}])
								}
						},
						showSymbol: false,
						data:  [],
						type: 'line',
						smooth: true
					}]
				},

				/*豆粕*/
				optionDouPo:{
				//backgroundColor:'#ffffff',
					xAxis: {
						type: 'category',
						axisLine: {
	                        lineStyle: {
	                            color: '#e4e4e4'
	                        }
	                    },
	                    axisLabel:{
	                        textStyle: {
	                            color: '#7b7d7f'
	                        }
	                    },
						data: []
					},
					yAxis: {
						type: 'value',
						color:'#7b7d7f',
						axisLine:{       //y轴
	                      show:false,
	                    },
	                    axisTick:{       //y轴刻度线
	                      show:false
	                    },
	                    axisLabel:{
	                        textStyle: {
	                            color: '#7b7d7f'
	                        }
	                    },
	                    splitLine: {
	                       lineStyle: {
	                            color: '#e4e4e4'
	                        }
	                    },
	                    min:0,
					},
					grid:{
	                    x:50,
	                    y:20,
	                    x2:5,
	                    y2:20
	                },
					tooltip: {
						trigger: 'axis'
					},
					series: [{
					itemStyle: {
                        normal: {
                           /* color: "#1cbc2d"*/
                           color:'#fe7f1e'
                        }
                    },
					lineStyle: {
						normal: {
							width: 3,
							/*shadowColor: '#c4e7c2',*/
							shadowColor: '#ffdcd0',
							shadowBlur: 3,
							shadowOffsetY: 6
						}
					},
					areaStyle: {
							normal: {
								color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
									offset: 0,
									color: '#fff2ee'
								}, {
									offset: 1,
									color: '#ffffff'
								}])
							}
					},
					showSymbol: false,
					data: [],
					type: 'line',
					smooth: true
					}]
				},





			}
		},
		methods:{
			/*抽取图标所需的数据*/
            extractChartData(obj,target){
                let Arr = [];
                    for(let i=0; i<obj.length; i++){
                        Object.keys(obj[i]).forEach(function(key,value){
                            if(key == target){
                                Arr.push(obj[i][key])
                            }
                           // console.log(key,obj[i][key]);
                        });
                    }
                //console.log(Arr)
                return Arr
            },
            /*截取X轴日期*/
            makeDateX(obj){
                let dateX = []
               for(let i=0; i<obj.length; i++){
                    dateX.push(obj[i].substring(5).replace("-","/"))
                   //console.log(obj[i].substring(5).replace("-","/")) 
                  // console.log(obj[i].slice(0,5))
               }
               return dateX
            },
            /*取数组最小值*/
            getMix (obj){
                let mixArr =  Math.min.apply(null, obj);
                return mixArr
            },
            /*截取图表上部数据*/
            getCharTitleData(target,obj){
            	let val='';
                for(let i in obj){
                	if (target == i) {
                		if(obj[i] == null){
                			val = "0"
                		}else{
                			val = obj[i]
                		}
                		
                	}

                }
                return val
            },

			/*获取轮播数据*/
			getCarouselData(){
				this.axios.get('/api/flxoa/CropOfferBroadcast',{
                    params:{
                       /*OfferDateStart:'2017-7-26'*/
                    }
                }).then(res => {
                	if(res.status == '200'){
                		console.log("轮播成功"+res)
	                    let OrArr = res.data[0]
	                    let LArr = OrArr.slice(0,(OrArr.length/2))
	                    let RArr = OrArr.slice((OrArr.length/2))
	                    this.bannerListLeft = LArr
	                    this.bannerListRight = RArr
	                    this.$nextTick(() => {  // 下一个UI帧再初始化swiper
					       this.initSwiper();
					     });
                	}else{
                		return
                	}
                                        
                },err => {
                     console.log("轮播失败"+err)
                })
                .catch(err=> {
                    console.log("捕获的错误")
                })
			},

			/*获取走势图数据豆油*/
			getMarketDateDouYou(kindId){
                this.axios.get('/api/flxoa/CropOfferIcon',{
                    params:{
                        listChart:kindId,
                       /* OfferDateStart:'2017-6-27',*/
                    }
                })
                .then(res => {
                    //console.log("首页豆油图表数据成功"+JSON.stringify(res))
                    let data = res.data[0].data;
                    let titleData = res.data[0].data[0]
                 	this.soyaBeanOilNum = this.getCharTitleData('Price',titleData)
                 	this.soyaBeanOilUpDow = this.getCharTitleData('fiveDayTrend',titleData)
	                 this.optionDouYou.xAxis.data = this.makeDateX(this.extractChartData(data,"OfferDate"))
	                 this.optionDouYou.series[0].data = this.extractChartData(data,"Price")
	                 this.optionDouYou.yAxis.min = this.getMix(this.extractChartData(data,"Price"))
	                 this.draMarketDate('chart',this.optionDouYou)
                },err =>{
                    console.log("首页豆油图表数据成功"+res)
                })
                .catch(err =>{
                     console.log("捕获的错误")
                })
			},

			/*获取走势图数据豆油*/
			getMarketDateDouPo(kindId){
                this.axios.get('/api/flxoa/CropOfferIcon',{
                    params:{
                        listChart:kindId,
                       /* OfferDateStart:'2017-6-27',*/
                    }
                })
                .then(res => {
                    //console.log("首页豆油图表数据成功"+JSON.stringify(res))
                    let data = res.data[0].data;
                    let titleData = res.data[0].data[0]
                    console.log(titleData)
                 	this.beanPulpNum = this.getCharTitleData('Price',titleData)
                 	this.beanPulpNumUpDow = this.getCharTitleData('fiveDayTrend',titleData)
	                 this.optionDouPo.xAxis.data = this.makeDateX(this.extractChartData(data,"OfferDate"))
	                 this.optionDouPo.series[0].data = this.extractChartData(data,"Price")
	                 this.optionDouPo.yAxis.min = this.getMix(this.extractChartData(data,"Price"))
	                 this.draMarketDate('chartDouPo',this.optionDouPo)
                },err =>{
                    console.log("首页豆油图表数据成功"+res)
                })
                .catch(err =>{
                     console.log("捕获的错误")
                })
			},
			goDetail(q){
				this.$router.push({name:'SanDaZhuLiang_Cont', query:{q:q}})
			},
			draMarketDate(domId,option){
				let myChart = echarts.init(document.getElementById(domId))
			      // 绘制图表
			      myChart.setOption(option);
			      setTimeout(function (){
					    window.onresize = function () {
					        myChart.resize();
					        //alert(666)
					        
					    }
				  },2000)

		    },
		    Search(){
		    	console.log(window.location.href );
		    	 var goUrl =  window.location.href; 
               window.location.href = '/page/YouzhiYouLiaoZHonglei.html?url_addres='+goUrl+'&ser_key=';
		    },

		    /*初始化轮播*/
			initSwiper() {
				var swiper = new Swiper('.swiper-container', {
			      direction: 'vertical',
			      loop: true,
			      initialSlide :0,
			      observer:true,//修改swiper自己或子元素时，自动初始化swiper
    			  observeParents:true,//修改swiper的父元素时，自动初始化swiper
			      speed:800,
			      autoplay: {
				        delay: 3000,
				        disableOnInteraction: false,
				     },
			    });
			},

			


			
			
		},
		components:{
			'v-headr':Headr,
			
		},
		mounted(){






		},
		created(){
			this.getCarouselData()
			this.getMarketDateDouYou('0435')
			this.getMarketDateDouPo('0544')

		},




	}
</script>

<style type="text/css" >
	a{color: inherit}
</style>





