<!-- 集市下面的活跃市场组件 -->
<template>
	<div id="HuoYueShiChang">
		<!-- <v-headr :myname="myname"></v-headr> -->
		<header class="head-container title-container header_title" >
            <div class="aux_back"  @click="backPrv()"></div>
            <p class="message-title text-cent">{{titleText}}</p>    
        </header>
		
	    <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10"  class="wrip-con">
                <div class="aui-card-list">
                        <a  href="javascript:void(0);"
                           class="aui-panel-cell"
                           v-for="key in datas"> 
                            <div class="aui-panel-cell-bd">
                                <h4>{{key.HxCropofferStr}}</h4>
                                <span>{{key.OfferDate}}</span>
                            </div> 
                            <div class="float-rigt">
                                <em class="jiage">￥{{key.Price}}元</em>
                            </div>
                        </a>
                </div>
            </div>
            <img src="../../assets/img/Loading.gif" class="loading" v-if="loading">
	    
	</div>
</template>

<script type="text/javascript">
	import Headr from '../../components/header.vue' 
	export default {
		name:'HuoYueShiChangD',
		data(){
			return {
				myname:{
					name:'活跃市场',
					IsSubPage:true
				},
				msg:'集市下面的活跃市场组件',
				titleText:'',
                datas:[],
                busy:false,
                loading:true,
                PerPageNum:20,
                PageNum:0,
			}
		},
		methods: {
			backPrv(){
                this.$router.go(-1)
            },

            loadMore() {
                //alert("++")
                 this.busy = true;
                 this.getLIst(true);
                  setTimeout(() => {
                    //console.log(this.PageNum)
                    this.PageNum++;
                   // console.log(this.PageNum)
                    this.busy = false;
                  }, 3000);
            },


			getLIst(flag){
                 //this.$indicator.open();
                let qStr = this.$route.query.q; 
                 console.log("==========="+qStr)               
                this.axios.get('/api/flxoa/CropOffer',{
                   params:{
                        PerPageNum:this.PerPageNum,
                        PageNum:this.PageNum,
                        CropCategoryCode:qStr,
                    }

                }).then(res => {
                    console.log("活跃市场详情成功"+res)
                    console.log(res.data[0].data)
                    this.titleText = res.data[0].data[0].CropCategoryName
                    //this.$indicator.close();
                    
                    if(flag){
                         this.datas =  this.datas.concat(res.data[0].data)
                         if(res.data[0].data.length < this.PerPageNum){
                            this.busy = true
                            this.loading = false
                         }else{
                            this.busy = false
                         }
                    }else{
                        this.datas = res.data[0].data
                        this.busy = false
                    }

                },err => {
                     console.log("活跃市场详情失败"+err)
                })
                .catch(err=> {
                    console.log("捕获的错误")
                })
               

               
             },


             
	      

	    },
	    components:{
			'v-headr':Headr,
		},
		mounted(){
		  	//console.log(this.$route.query.q)
		  	this.getLIst()

		  

		},
		created(){

		},
	}
</script>
<style type="text/css">
	
	   .loading{
        display: block;
        margin: 0 auto;
    }
    .wrip-con{
        margin-top: 44px;
    }
</style>


